import { SERIALIZE } from "../Utils";
export class Size {
    constructor(width, height) {
        this.width = width;
        this.height = height;
    }
    [SERIALIZE]() {
        return [this.width, this.height];
    }
    clone() {
        return new Size(this.width, this.height);
    }
}
