import Document from "../Document";
import { Color, StarShape, SolidBrush, Rectangle, Point, Size } from "../../Graphics";
import { DefaultMiddleware } from "../Middleware";
import { StarElement } from "../Elements";
export default class MasterDocument extends Document {
    constructor(size = new Size(900, 600), middleware = new DefaultMiddleware()) {
        super(middleware);
        this._size = size;
        this._board = Rectangle.fromSize(size);
    }
    get size() {
        return this._size;
    }
    get globalBounds() {
        if (this._globalBounds !== null) {
            return this._globalBounds;
        }
        return this._globalBounds = Rectangle.merge([this._board, super.globalBounds]);
    }
    get globalTightBounds() {
        if (this._globalTightBounds !== null) {
            return this._globalTightBounds;
        }
        return this._globalTightBounds = Rectangle.merge([this._board, super.globalTightBounds]);
    }
    test() {
        // TODO: remove this
        const p = new Skia.SkPath();
        p.addRect(new Rectangle(0, 0, 200, 100));
        for (let i = 0; i < 10; i++) {
            const gr = this.createGroupElement();
            gr.title = "Group " + (i + 1);
            gr.position = new Point(i * 10, i * 10);
            gr.scale = new Point(2, 2);
            for (let j = 0; j < 30; j++) {
                //const sh = this.createShapeElement(p);
                const sh = this.createStarElement(Math.trunc(3 + Math.random() * 10)); // this.createPathElement(Path.fromString("M 130 110 C 120 140, 180 140, 170 110"));
                sh.position = new Point(Math.random() * 8000 - 2000, Math.random() * 8000 - 2000);
                sh.fill = new SolidBrush(Color.green);
                sh.title = "Path " + (i + 1) + " - " + (j + 1);
                gr.appendChild(sh);
            }
            this.appendChild(gr);
        }
    }
    createStarElement(sides) {
        return new StarElement(this, new StarShape(5, 150, 0.25));
    }
    render(context) {
        context.fillRect(this._board, SolidBrush.WHITE);
        this.draw(context);
    }
    cloneCurrent() {
        return new MasterDocument(this._board, this._middleware);
    }
}
