        const old = this.canvasElement;
        const width = old.width / dpr;
        const height = old.height / dpr;
        const canvas = old.cloneNode(false);
        old.replaceWith(canvas);
        this.resizeCanvas(width, height, dpr);
        this.connectCanvas(canvas);
