import Serializer from "./Serializer";
import Unserializer from "./Unserializer";
import WritableDataView from "./WriteableDataView";
import ReadableDataView from "./ReadableDataView";
import repository from "./Repository";
import "./Native";
export { SERIALIZE, UNSERIALIZE } from "./Types";
export { repository };
export function serializable(constructor, name) {
    return repository.registerSerializable(constructor, name);
}
export function serialize(data, initialBufferSize = 512) {
    return (new Serializer(new WritableDataView(initialBufferSize))).encode(data);
}
export function unserialize(data) {
    return (new Unserializer(new ReadableDataView(data))).value();
}
