import { Color, DefaultPen, SolidBrush } from "../Graphics";
import { ShapeElement } from "../Model";
import BaseTool from "./BaseTool";
export default class SelectTool extends BaseTool {
    constructor() {
        super(...arguments);
        this.pen = new DefaultPen(new SolidBrush(Color.red));
        this.selectPen = new DefaultPen(new SolidBrush(Color.blue));
        this.selectFill = new SolidBrush(Color.blue, 0.25);
        this.position = null;
        this.hoverElement = null;
        this.selectionRectangle = null;
    }
    get name() {
        return "select";
    }
    render(engine) {
        if (!this.isInvalidated) {
            return;
        }
        this.drawSnapshotImage(engine, false);
        this.drawSelectedElements(engine);
        this.drawHoverElements(engine);
        this.drawSelectionRectangle(engine);
        engine.context.flush();
        this.isInvalidated = false;
    }
    onMouseHover(engine, event) {
        const prev = this.hoverElement;
        this.hoverElement = engine.document.getElementAt(event.position);
        if (prev !== this.hoverElement) {
            this.isInvalidated = true;
        }
    }
    onMouseLeftButtonDown(engine, event) {
        const node = engine.document.getElementAt(event.position);
        if (node !== null) {
            this.position = event.position;
            //engine.selection.toggle(node, this.keyboardStatus.isCtrl);
            engine.selection.select(node, this.keyboardStatus.isCtrl || engine.selection.isSelected(node));
            this.isInvalidated = true;
            // TODO: Emit de event
            return;
        }
        this.pivot = event.position;
        this.selectionRectangle = this.getSelectionRectangle(this.pivot, event.position, engine.matrix);
        engine.selection.clear();
        this.isInvalidated = true;
        // TODO: Emit de event
    }
    onMouseLeftButtonMove(engine, event) {
        if (this.pivot !== null) {
            this.selectionRectangle = this.getSelectionRectangle(this.pivot, event.position, engine.matrix);
            this.isInvalidated = true;
            return;
        }
        if (this.position !== null && !this.position.equals(event.position)) {
            const sub = event.position.sub(this.position);
            this.position = event.position;
            for (let element of engine.selection) {
                engine.document.middleware.globallyMoveBy(element, sub.x, sub.y);
            }
            this.invalidate();
            return;
        }
    }
    onMouseLeftButtonUp(engine, event) {
        if (this.pivot) {
            engine.selection.rectSelect(this.getSelectionRectangle(this.pivot, event.position, engine.document.globalMatrix), engine.document);
            this.selectionRectangle = null;
            this.pivot = null;
            this.isInvalidated = true;
            // TODO: Emit de event
            return;
        }
        if (this.position !== null) {
            this.position = null;
            engine.project.state.snapshot();
            this.invalidate();
            return;
        }
        this.pivot = null;
    }
    drawSelectedElements(engine) {
        if (engine.selection.size === 0) {
            return;
        }
        const { context, zoom } = engine;
        context.save();
        context.multiplyMatrix(engine.matrix);
        this.selectPen.width = 1 / zoom;
        for (let node of engine.selection) {
            context.strokeRect(node.globalTightBounds, this.selectPen);
        }
        context.restore();
    }
    drawHoverElements(engine) {
        if (this.hoverElement === null) {
            return;
        }
        if (this.hoverElement instanceof ShapeElement) {
            const context = engine.context;
            context.save();
            context.multiplyMatrix(engine.matrix);
            this.pen.width = 1 / engine.zoom;
            context.strokePath(this.hoverElement.globalPath, this.pen);
            context.restore();
        }
    }
    drawSelectionRectangle(engine) {
        if (this.selectionRectangle !== null && this.selectionRectangle.isVisible) {
            engine.context.fillRect(this.selectionRectangle, this.selectFill);
        }
    }
}
