import Node from "./Node";
import { NormalIsolatedComposition, Matrix } from "../Graphics";
import { PathElement, GroupElement } from "./Elements";
import { uuid } from "../Utils";
export default class Document extends Node {
    constructor(middleware) {
        super(null);
        this._document = this;
        this._middleware = middleware;
    }
    get supportsChildren() {
        return true;
    }
    get middleware() {
        return this._middleware;
    }
    getElementById(id) {
        const find = (parent) => {
            for (let n = parent.firstChild; n !== null; n = n.nextSibling) {
                if (n.id === id) {
                    return n;
                }
                if (n.supportsChildren) {
                    const ret = find(n);
                    if (ret) {
                        return ret;
                    }
                }
            }
            return null;
        };
        return find(this);
    }
    getElementAt(point) {
        for (const element of this.children(true)) {
            const el = element.getElementAt(point);
            if (el) {
                return el;
            }
        }
        return null;
    }
    adoptNode(node) {
        if (node.document !== this) {
            node._document = this;
            for (let n = node.firstChild; n !== null; n = n.nextSibling) {
                this.adoptNode(n);
            }
        }
        return node;
    }
    get globalMatrix() {
        if (this._globalMatrix === null) {
            this._globalMatrix = this.localMatrix;
        }
        return this._globalMatrix;
    }
    getLocalMatrix() {
        return new Matrix();
    }
    createGroupElement() {
        // return new ClipPathElement(this, Path.fromString("M0 0H400V500H-100Z"));
        return new GroupElement(this);
    }
    createPathElement(path) {
        return new PathElement(this, path);
    }
    generateUniqueId() {
        return uuid('-'); // TODO: check if unique inside document
    }
    draw(context) {
        // context.save();
        context.drawPicture(this.getPicture(), NormalIsolatedComposition);
        // context.restore();
    }
    serialize(data) {
        data.constructor = [this._middleware];
    }
    unserialize(data) {
    }
}
