export class State {
    constructor(project, size = 10) {
        this.undoList = [];
        this.redoList = [];
        this.project = project;
        this.size = size;
        this.record = project.document.clone();
    }
    get canUndo() {
        return this.undoList.length > 0;
    }
    get canRedo() {
        return this.redoList.length > 0;
    }
    undo() {
        if (!this.canUndo) {
            return false;
        }
        const current = this.project.document;
        const next = this.undoList.pop();
        this.redoList.push(current);
        this.record = next.clone();
        return this.project.replace(current, next);
    }
    redo() {
        if (!this.canRedo) {
            return false;
        }
        const current = this.project.document;
        const next = this.redoList.pop();
        this.undoList.push(current);
        this.record = next.clone();
        return this.project.replace(current, next);
    }
    snapshot() {
        while (this.redoList.length) {
            this.redoList.pop().dispose();
        }
        this.undoList.push(this.record);
        this.record = this.project.document.clone();
        if (this.undoList.length > this.size) {
            this.undoList.shift().dispose();
        }
    }
    dispose() {
        while (this.undoList.length) {
            this.undoList.pop().dispose();
        }
        while (this.redoList.length) {
            this.redoList.pop().dispose();
        }
        this.record.dispose();
    }
}
