import { SERIALIZE, UNSERIALIZE } from "../Utils";
import { State } from "./State";
export class Project {
    constructor(document) {
        this._documentList = null;
        this._masterDocument = this._document = document;
        this._state = new WeakMap();
        this._state.set(document, new State(this));
    }
    get documentList() {
        if (this._documentList === null) {
            this._documentList = new Set();
        }
        return this._documentList;
    }
    get masterDocument() {
        return this._masterDocument;
    }
    get document() {
        return this._document;
    }
    set document(value) {
        if (this._masterDocument === value || this._documentList.has(value)) {
            this._document = value;
        }
    }
    replace(current, next) {
        if (this._document !== current) {
            return false;
        }
        const state = this._state.get(current);
        this._state.delete(current);
        this._state.set(next, state);
        this._document = next;
        if (this._masterDocument === current) {
            this._masterDocument = next;
            return true;
        }
        // At this stage this should never be null
        this._documentList.delete(current);
        this._documentList.add(next);
        return true;
    }
    get state() {
        if (!this._state.has(this._document)) {
            this._state.set(this._document, new State(this));
        }
        return this._state.get(this._document);
    }
    [SERIALIZE]() {
        const data = {};
        this.serialize(data);
        return data;
    }
    [UNSERIALIZE](data) {
        this.unserialize(data);
    }
    serialize(data) {
        data.constructor = [this._document];
        data.documents = [this._documentList];
    }
    unserialize(data) {
        [this._documentList] = data.documents;
    }
    dispose() {
        for (const document of this._documentList) {
            this._state.get(document).dispose();
            document.dispose();
        }
        this._state.get(this._masterDocument).dispose();
        this._masterDocument.dispose();
        this._documentList.clear();
    }
}
