export default class DefaultMiddleware {
    blend(element, value) {
        if (element.blend === value) {
            return false;
        }
        element.blend = value;
        return true;
    }
    isolate(element, value) {
        if (!element.supportsChildren || element.isolate === value) {
            return false;
        }
        element.isolate = value;
        return true;
    }
    fill(element, value) {
        // TODO: check fill equality
        // TODO: save opacity???
        element.fill = value;
        return true;
    }
    fillOpacity(element, value) {
        if (element.fillOpacity === value) {
            return false;
        }
        element.fillOpacity = value;
        return true;
    }
    fillRule(element, value) {
        if (element.fillRule === value) {
            return false;
        }
        element.fillRule = value;
        return true;
    }
    opacity(element, value) {
        if (element.opacity === value) {
            return false;
        }
        element.opacity = value;
        return true;
    }
    orientation(element, value) {
        if (element.orientation === value) {
            return false;
        }
        element.orientation = value;
        return true;
    }
    position(element, value) {
        if (element.position.equals(value)) {
            return false;
        }
        element.position = value;
        return true;
    }
    rotate(element, value) {
        if (element.rotate === value) {
            return false;
        }
        element.rotate = value;
        return true;
    }
    scale(element, value) {
        if (element.scale.equals(value)) {
            return false;
        }
        element.scale = value;
        return true;
    }
    skewAngle(element, value) {
        if (element.skewAngle === value) {
            return false;
        }
        element.skewAngle = value;
        return true;
    }
    skewAxis(element, value) {
        if (element.skewAxis === value) {
            return false;
        }
        element.skewAxis = value;
        return true;
    }
    strokeBrush(element, value) {
        element.strokeBrush = value;
        return true;
    }
    strokeDashArray(element, value) {
        // TODO: check if equals
        element.strokeDashArray = value;
        return true;
    }
    strokeDashOffset(element, value) {
        if (element.strokeDashOffset === value) {
            return false;
        }
        element.strokeDashOffset = value;
        return true;
    }
    strokeLineCap(element, value) {
        if (element.strokeLineCap === value) {
            return false;
        }
        element.strokeLineCap = value;
        return true;
    }
    strokeLineJoin(element, value) {
        if (element.strokeLineJoin === value) {
            return false;
        }
        element.strokeLineJoin = value;
        return true;
    }
    strokeLineWidth(element, value) {
        if (element.strokeLineWidth === value) {
            return false;
        }
        element.strokeLineWidth = value;
        return true;
    }
    strokeMiterLimit(element, value) {
        if (element.strokeMiterLimit === value) {
            return false;
        }
        element.strokeMiterLimit = value;
        return true;
    }
    strokeOpacity(element, value) {
        if (element.strokeOpacity === value) {
            return false;
        }
        element.strokeOpacity = value;
        return true;
    }
    anchor(element, value) {
        if (element.anchor.equals(value)) {
            return false;
        }
        element.anchor = value;
        return true;
    }
    paintOrder(element, value) {
        if (element.paintOrder === value) {
            return false;
        }
        element.paintOrder = value;
        return true;
    }
    setProperty(elements, method, value) {
        let changed = 0;
        for (const element of elements) {
            if (method.call(this, element, value)) {
                changed++;
            }
        }
        return changed;
    }
    swapColors(element, keepOpacity = true) {
        element.swapColors(keepOpacity);
    }
    globallyMoveBy(element, dx, dy) {
        return element.globallyMoveBy(dx, dy);
    }
    globallyMoveTo(element, x, y) {
        return element.globallyMoveTo(x, y);
    }
}
