import { clamp, round } from "./number";
export function interpolateNumber(percent, from, to) {
    return from + percent * (to - from);
}
export function interpolatePercent(percent, from, to) {
    return clamp(interpolateNumber(percent, from, to), 0, 1);
}
export function interpolateColorComponent(percent, from, to) {
    return clamp(Math.round(from + percent * (to - from)), 0, 255);
}
export function interpolateAlphaComponent(percent, from, to) {
    return clamp(round(interpolateNumber(percent, from, to)), 0, 1);
}
